/*------------------------------------*\
    styles
\*------------------------------------*/

.blog {
}


.blog__normal-flow {
    max-width: 640px;
    margin: 0 auto;
    position: relative;
    img {
        box-shadow: 0 0 0 1px rgba(black,.07);
        margin: 25px 0 25px 0;
    }
}

.blog__widescreen-flow {
    margin: 160px 0;
    img {
        width: 100%;
        border-radius: none;
        box-shadow: none;
    }
}




.blog__content {
    background-color: $clr-gray-bg;
    overflow: auto;


    hr {
        display: block;
        height: 1px;
        background-color: rgba(black,.15);
        margin: 160px 30% 100px 30%;
    }


    header {
        margin-top: 160px;

        time {
            //font-family: "Karla", sans-serif;
            font-size: 16px;
            color: rgba(black,.5);
        }

        h1 {
            margin-top: 15px;
            margin-bottom: 20px;
        }

        p {
            margin-top: 20px;
            letter-spacing: .7px;
            color: rgba(black,.5);
            font-size: 18px;
        }

        a {
            color: rgba(black,.5);
            font-weight: 400;
            @include cool-underline(rgba(black,.4), rgba(black,.4), $clr-gray-bg, 95%);
        }
    }

    p {


        //font-family: "Work Sans", sans-serif;
        font-size: 20px;
        line-height: 1.5;
        color: rgb(100,100,100);
        margin: 25px 0 25px 0;

        a {
            @include cool-underline(gray, rgba(black,.3), $clr-gray-bg, 90%);
            //font-weight: 500;
        }

    }


    img {
        display: block;
        max-width: 100%;
        border-radius: 3px;
        //box-shadow: 0 0 25px rgba(black,.01);
    }



    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        //font-family: "Karla", sans-serif;
        font-weight: 700;

    }


    h1 {
        font-size: 50px;
        margin: 100px 0 25px 0;
        line-height: 1.2;
        font-weight: 700;
    }

    h2 {
        font-size: 36px;
        margin: 180px 0 25px 0;
    }


    h3,
    h4,
    h5,
    h6 {
        font-size: 24px;
        margin: 80px 0 25px 0;
    }




    h2 {
        & + h3,
        & + h4,
        & + h5,
        & + h6 {
            margin-top: 40px;
        }
    }


    abbr {
        text-decoration: none;
        border: none;

        &::after {
            content: "?";
            font-size: 12px;
            position: relative;
            top: -9px;
            font-weight: 500;
            display: inline-block;
            color: #222;

            transition: opacity 300ms ease-in-out;
            opacity: .3;

        }


        &:hover::after {
            opacity: 1;
        }


    }


    hr {
        & + h3,
        & + h4,
        & + h5,
        & + h6 {
            margin-top: 160px;
        }
    }



    /*
    a {

        //transition: color 300ms ease-in-out, background-image 300ms ease-in-out;

        &:hover {
            background-image: linear-gradient(to right, #222, #222);
            color: #222;
        }

    }
    */



    aside {

        a {
            @include cool-underline(gray, gray, $clr-gray-bg, 95%);
            //font-weight: 500;
        }


        //font-family: "Work Sans", sans-serif;
        position: absolute;
        left: -285px;
        font-size: 16px;
        width: 250px;
        text-align: right;
        padding-top: 3px;
        opacity: .2;

        //font-weight: 300;

        transition: opacity 300ms ease-in-out;
        &:hover {
            opacity: .8;
        }

    }

    pre {
        //background-color: rgb(240,240,240);
        color: gray;
        padding: 10px 15px;
        margin: 40px 0 40px 0;

        code {
            font-size: 18px;
        }
    }

    code {
        font-family: "Ubuntu Mono", monospace, monospace;
        //background-color: rgb(240,240,240);
        font-size: 20px;
    }


    li {
        @extend p;
    }

    ul {
        list-style-type: none;

        li {
            display: block;
            position: relative;
            padding-left: 40px;
        }

        li::before {
            width: 30px;
            text-align: right;
            font-weight: 500;
            opacity: .3;
            position: absolute;
            left: 0;
            content: "•";
        }
    }

    ol {
        counter-reset: ordered-item;
        list-style-type: none;

        li {
            display: block;
            position: relative;
            padding-left: 40px;
        }

        li::before {
            width: 30px;
            text-align: right;
            font-weight: 500;
            opacity: .5;
            position: absolute;
            left: 0;
            content: counter(ordered-item) ".";
            counter-increment: ordered-item;
        }
    }

    strong {
        font-weight: 500;
    }


    blockquote {
        margin: 50px;
        text-align: center;

        p {
            font-size: 28px;
            //font-family: "Karla", sans-serif;
            color: rgba(black,.8);
            margin: 0;
            font-weight: 500;
        }

        a {
            @include cool-underline(rgba(black,.2), rgba(black,.2), $clr-gray-bg, 93%);
        }



        footer {
            display: inline-block;
            position: relative;
            margin: 0;
            padding: 0;
            border: 0;
            font-size: 20px;
            //font-family: "Karla", sans-serif;
            text-align: center;
            font-style: italic;
            color: rgba(black,.5);

            &::before {
                position: absolute;
                top: 8px;
                left: -45px;
                content: "\”";
                font-size: 90px;
                line-height: .7;
                color: rgba(black,.1);
            }

        }


    }


    footer {

        padding: 50px 20px;
        margin-top: 100px;

        border-top: 1px solid rgba(black,.05);
    }

}




/*------------------------------------*\
    states
\*------------------------------------*/




/*------------------------------------*\
    media-queries
\*------------------------------------*/

@media screen and (max-width: 1300px) {
    .blog__content {
        aside {
            left: -235px;
            width: 200px;
        }
    }
}


@media screen and (max-width: 1200px) {
    .blog__content {
        aside {
            left: -175px;
            width: 140px;
        }
    }
}

@media screen and (max-width: 1050px) {
    .blog__content {
        aside {
            display: none;
        }
    }
}


@media screen and (max-width: 1000px) {




    .blog__normal-flow {
        padding: 0 20px;
    }


    .blog__widescreen-flow {
        margin: 100px 0;
    }



    .blog__content {


        hr {
            margin-top: 50px;
            margin-bottom: 0;
        }

    }

}



